.Order {
  padding: 20px;

  .backLink {
    text-decoration: none;
    color: #61dafb;
    font-size: 20px;

    &:visited {
      color: #61dafb;
    }
  }
  .point {
    display: grid;
    .label {
      span {
        color: #6b3804;
      }
      line-height: 30px;
    }
    .labelDescription {
      color: #f1f1f1;
      font-size: 12px;
      line-height: 15px;
    }
    .inputField {
      height: max-content;
      input {
        height: 30px;
        padding-left: 5px;
        border-radius: 5px;
        width: 250px;
        &[type="checkbox"] {
          height: 20px;
          width: 20px;
        }
      }
    }
  }
  .submit {
    height: 30px;
  }
  .Select {
    border-image-source: initial;
    border-image-slice: initial;
    border-radius: 5px;
    border-image-repeat: initial;
    border-style: inset;
    border-color: initial;
  }

  .messagePopup {
    padding: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    word-wrap: break-word;
    font-size: 1.125em;
    flex-direction: column;

    button {
      margin: 20px;
      height: 35px;
      width: 60px;
      background-color: #ced6d8;
      cursor: pointer;
      font-size: 1.125em;
      box-shadow: -8px -8px 12px 0 rgba(255, 255, 255, 0.3),
        12px 12px 16px 0 rgba(0, 0, 0, 0.25);
      &:active {
        box-shadow: inset 12px 12px 16px 0 rgba(0, 0, 0, 0.25),
          inset -8px -8px 12px 0 rgba(255, 255, 255, 0.3);
      }
    }
  }

  .orderPositions {
    max-width: 300px;
    padding: 10px;
    margin: 20px 0 ;

    .posBlock {
      .legend {

      }
      padding: 10px;
      display: flex;
      max-width: 250px;

      justify-content: space-around;
      align-items: center;
    }
    border: 2px solid black;
    border-radius: 10px;
    img {
      width: 60px;
      height: 60px;

    }
    .orderSum {
      margin-top: 20px;
    }
  }
}

//desktop
@media (min-width: 767px) {
  .Order {
    .Select {
      width: 309px;
    }
    .point {
      height: 50px;

      .inputField {
        height: max-content;
        //input {
        //  width: 300px;
        //}
      }
      padding-top: 20px;

      grid-template-columns: 200px 300px;
    }
  }
}

//mobile
@media (max-width: 766px) {
  .Order {
    .point {
      .Select {
        width: calc(100vw - 31px);
      }
      .labelDescription {
        display: inline;
        margin-left: 10px;
      }
      .inputField {
        height: max-content;
        display: flex;
        justify-content: center;
        //input {
        //  width: calc(100vw - 40px);
        //}
      }
      grid-template-rows: 25px 70px;
    }
  }
}
